:root{
  font-size: 24px;
}

.app {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: cornflowerblue;
  user-select: none;
}

.app::before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background: white;
  opacity: 0;
  transform: translateY(100%);
  transition: opacity .5s, transform .5s;
}

.app.loading::before{
  opacity: 1;
  transform: translateY(0);
}

.paper,
.writable-paper {
  position: relative;
  background: greenyellow;
  text-align: center;
  padding: 10px;
  max-width: 50vw;
  max-height: 50vw;
  box-shadow: black 2px 2px 2px;
  width: 300px;
  height: 300px;
}

.paper {
  text-overflow: clip;
  word-wrap: break-word;
  vertical-align: middle;
  overflow-y: hidden;
}

.writable-paper {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  box-shadow: black 0 0 10px 5px;
}


.writable-paper>textarea {
  max-width: 100%;
  max-height: 100%;
  text-align: center;
  border: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  background-color: transparent;
  font-size: 1rem;
}

.writable-paper>textarea:focus {
  outline: 0;
}

.used {
  position: fixed;
  bottom: 0;
  left: 0;
  transition: transform .5s;
  transform: rotateZ(6deg) translateY(calc(100% - 10px));
}

.used:hover {
  transform: rotateZ(6deg) translateY(5%);
}

.used::before {
  content: "\2199";
  color: red;
  text-shadow: black 2px 2px;
  text-align: right;
  font-size: 5rem;
  font-weight: bold;
  position: absolute;
  right: 0;
  width: 100%;
  height: 100%;
  transform: rotateZ(-6deg) translate(20px, calc(-5rem - 20px));
  transition: opacity .5s;
}

.used:hover::before {
  opacity: 0;
}

.used>button[type=submit] {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(50%, -50%);
  background: green;
  border-radius: 50%;
  box-sizing: border-box;
  padding: 0;
  border: 0;
  overflow: hidden;
  box-shadow: black 2px 2px 2px;
}

.used>button[type=submit]:focus {
  outline: solid limegreen 2px;
}

.used>button[type=submit]:active {
  background: darkgreen;
}

.used>button[type=submit]:disabled {
  opacity: .5;
}

.used>button[type=submit]::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  margin: 5px;
  background: linear-gradient(to right,
      transparent 0,
      transparent calc(50% - 2.5px),
      white calc(50% - 2.5px),
      white calc(50% + 2.5px),
      transparent calc(50% + 2.5px)),
    linear-gradient(to bottom,
      transparent 0,
      transparent calc(50% - 2.5px),
      white calc(50% - 2.5px),
      white calc(50% + 2.5px),
      transparent calc(50% + 2.5px));
}

.pile-of-papers {
  display: flex;
  padding: 20px;
  flex-flow: row wrap;
  justify-content: center;
  overflow-y: auto;
  max-height: 100vh;
  box-sizing: border-box;
}

.pile-of-papers>.paper {
  width: 250px;
  height: 250px;
  margin: 0 10px 10px 0;
}